import { Component } from '@angular/core';
import {SystemSetupServiceService} from './service/apiService.service'
import {
  
  FormsModule,
  FormGroup

} from '@angular/forms';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  constructor(private systemSetupServiceService: SystemSetupServiceService) {}
  title = 'app';
  errorMessage: String;
  trackTrainingSeesionData=[]
  ngOnInit() {
    this.getApiDBDetails();


  }
  getApiDBDetails(): void {
    this.systemSetupServiceService.getApiDBDetails().subscribe((data) => {
     console.log("data",data)
     this.trackTrainingSeesionData=data
    },
      (error) => {
        this.errorMessage = <any>error
      
      });
  }
}
